<template>
  <div class="content">
    <div class="title">
      翻译
    </div>
    <div class="contentB">
      <input type="text" class="input-text" placeholder="输入英文/汉字" v-model="text">
      <br>
      <div class="button" @click="goTrans()">
        立即翻译
      </div>
      <div class="show-content">
        <p v-for="item in trans">
          {{item}}  <br>
        </p>
      </div>
    </div>
  </div> 
</template>

<script>
  export default {
    mounted(){
      
    },
    data(){
      return{
        apiUrl:"http://route.showapi.com/32-9",
        showapi_sign:"30112badab7b42aaae848f67f2bf3352",
        showapi_appid:"38548",
        data:"",
        trans:"",
        text:""
      }
    },
    created(){
      this.$store.dispatch("inOther");
    },
    methods:{
      goTrans(){
        let that = this;
        this.$http.post(
          this.apiUrl,
          {
            showapi_sign:this.showapi_sign,
            showapi_appid:this.showapi_appid,
            q:this.text
          },
          {emulateJSON:true}
        ).then(
          (res) => {
            console.log(res.showapi_res_body)
            that.trans = res.data.showapi_res_body.basic.explains
          }
        ),function (error) {
          console.log(error);
        }
      }
    }
  }
</script>
<style lang="less">
 .content{
   width: 100%;
   transform: scale3d(1, 1, 1);
   .title{
     width: 100%;
     background: #3e4243;
     height: 40px;
     line-height: 40px;
     font-size: 16px;
     text-align: center;
     color: #fff;
     border-radius: 5px;
     position: fixed;
     top: 0px;
   }
   .contentB{
     text-align: center;
     .input-text{
       border: 1px solid #bfcbd9;
       border-radius: 4px;
       color: #1f2d3d;
       font-size: 12px;
       height: 26px;
       outline: 0;
       padding: 3px 10px;
       width: 80%;
       margin: 20px auto;
     }
     .button{
       display: inline-block;
       background: #20a0ff;
       color: #fff;
       height: 20px;
       font-size: 12px;
       border-radius: 4px;
       text-align: center;
       line-height: 20px;
       padding: 5px 20px;
       cursor: pointer;
     }
     .show-content{
       margin: 20px;
       font-size: 12px;
       color: #3e4243;
     }
   }
 }

</style>



