<template>
  <div class="home">
    <div class="home_title">
      Application
    </div>
    <banner></banner>
    <navBox></navBox>
    <news></news>
  </div>
</template>

<script>
  import banner from '../components/banner.vue';
  import navBox from '../components/nav-box.vue';
  import news from '../components/news.vue';
  export default {
    name: 'home',
    components: {
      banner,
      navBox,
      news
    },
    created(){
      this.$store.dispatch("inHome");
    }
  }
</script>
<style lang="less">
  .home{
    width: 100%;
    height: auto;
    .home_title{
      height: 30px;
      background: #3e4243;
      font-size: 1em;
      line-height: 2em;
      text-align: center;
      color: #fff;
      border-radius: 5px;
    }
  }
</style>



