<template>
  <div class="my">
    <div class="my-head">
      <div class="my-logo"></div>
      <span class="my-name">aclrioe</span>
    </div>
    <div class="my-detail">
      github:  <a href="https://github.com/aclrioe">https://github.com/aclrioe</a>
      <br><br>
      E-mail: aclrioe@gmail.com
      <br> <br>
      QQ: 741419855
      <br> <br>
    </div>
  </div>
</template>

<script>
  export default {
    name:"my",
    created(){
      this.$store.dispatch("inMy")
    }
  }
</script>
<style lang="less">
  .my-head{
    width: 100%;
    height: 150px;
    background: #3e4243;
    position: relative;
    border-radius: 10px;
    .my-logo{
      display: block;
      width: 80px;
      height: 80px;
      border-radius: 50%;
      background: url("../assets/img/logo.png") no-repeat;
      position: absolute;
      top: 30px;
      left: 0;
      right: 0;
      margin: 0px auto;
    }
    .my-name{
      display: block;
      position: absolute;
      bottom: 15px;
      right: 0;
      left: 0;
      margin: 0px auto;
      text-align: center;
      color: #fff;
    }
  }
  .my-detail{
    margin: 10px 0;
    background: #f6f8fa;
    padding: 15px;
    border-radius: 10px;
    a{
      text-decoration: none;
      color: #a5a5a5;
    }
  }
</style>



